import { Component, Inject, ViewEncapsulation } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';

import { Util } from '../../../util/util';

@Component({
  moduleId: module.id,
  selector: 'o-fullscreen-dialog',
  template: `
    <mat-toolbar color="primary" class="mat-elevation-z5">
      <span fxFlex></span>
      <button type="button" mat-icon-button mat-dialog-close>
        <mat-icon svgIcon="ontimize:close"></mat-icon>
      </button>
    </mat-toolbar>
    <mat-dialog-content fxLayoutAlign="center center" class="o-fullscreen-dialog-content">
      <img [src]="imageSrc" />
    </mat-dialog-content>
  `,
  styles: [`
    .o-image-fullscreen-dialog-cdk-overlay .mat-dialog-container{padding:0}.o-image-fullscreen-dialog-cdk-overlay .mat-dialog-container .o-fullscreen-dialog{height:100%;display:block}.o-image-fullscreen-dialog-cdk-overlay .mat-dialog-container .o-fullscreen-dialog .mat-dialog-content.o-fullscreen-dialog-content{margin:12px;height:100%;max-height:calc(100% - 64px - 24px)}
  `],
  encapsulation: ViewEncapsulation.None,
  host: {
    '[class.o-fullscreen-dialog]': 'true'
  }
})
export class OFullScreenDialogComponent {

  imageSrc: any;

  constructor(
    public dialogRef: MatDialogRef<OFullScreenDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) {
    if (Util.isDefined(data)) {
      this.imageSrc = data;
    }
  }

}
